:host {
  display: block;
  width: 100%;
}

select {
  width: 100%;
  max-width: 100%;
  padding: 0.5rem;
  font-size: 1rem;
}

/* Responsive sizing */
@media (max-width: 768px) {
  select.small {
    font-size: 0.8rem;
  }

  select.medium {
    font-size: 1rem;
  }

  select.large {
    font-size: 1.2rem;
  }
}

.ParentSelect {
  /* Basic select element styling */
  width: 100%; /* Occupy full width of its container */
  padding: 8px 12px;
  border: 3px solid var(--theme-menu--border-color);
  border-radius: 4px;
  font-family: Arial, sans-serif; /* Example font */
  cursor: pointer;

}


.ParentSelect {
  background-color:var(--theme-menu--background);
  color: var( --theme-menu-header--color);
}

.ParentSelect option {
  background-color: var(--theme-menu-item--background);
  color: var(--theme-menu-item--color);
}

.ParentSelect option:hover{
  background-color: var(--theme-menu-item--background--hover);
  color: var(--theme-menu-item--color--hover);
}

/* --- Variant Size Specific Styles --- */

/* Small Size */
.ParentSelect.small {
  font-size: 12px;
  padding: 6px 10px;
}

/* Medium Size */
.ParentSelect.medium {
  font-size: 14px;
  padding: 8px 12px;
}

/* Large Size */
.ParentSelect.large {
  font-size: 16px;
  padding: 10px 14px;
}

