import { Component, Prop, h } from '@stencil/core';

@Component({
    tag: 'my-dropdown-menu',
    styleUrl: 'my-dropdown-menu.css',
    shadow: true,})
export class MyDropdownMenu {

    @Prop() options: string[] = [];
    @Prop() valeOfEachOption: string[] = [];

    @Prop() variantSize: 'small' | 'medium' | 'large' = 'medium';

    render()
    {
        const classOption = {
            "ParentSelect": true,

            'small': this.variantSize === 'small',
            'medium': this.variantSize === 'medium',
            'large': this.variantSize === 'large',
        };

        const optionsLength = this.options.length
        return (
          <>
            <ix-style-loader></ix-style-loader>
            <select class={classOption}  style={{ width: '100%' }}>
                  {Array.from({ length: optionsLength }).map((_, i) => (
                  <option value={this.valeOfEachOption[i] ?? this.options[i]}>{this.options[i]}</option>
                  ))}
              </select>
          </>
        );

    }
}
